import Vue from "vue";
import Vuex from "vuex";
import axois from "axios";

const baseUrl = "https://api.github.com";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    repos: [],
    searchResults: []
  },
  getters: {
    repos(state) {
      return state.repos;
    },
    searchResults(state) {
      return state.searchResults
    }
  },
  mutations: {
    setRepos(state, repositories) {
      state.repos = repositories;
    },
    setSearchResults(state, results) {
      console.log(results.length)
      state.searchResults = results;
    },
    resetSearchResults: (state) => state.searchResults = [],
    
  },
  actions: {
    fetchRepos({ commit }) {
      axois
      .get(baseUrl + "/users/program-om/starred?per_page=200")
      .then(({ data }) => {
        commit("setRepos", data);
      })
      .catch(err => console.log(err));
    },
    searchRepos({ commit }, searchText){
      axois
      .get( baseUrl + "/search/repositories?q=" + 
            searchText.split(' ').join('+') + 
            '&sort=stars&order=desc')
      .then( ({ data }) => {
        console.log(data)
        commit('setSearchResults', data.items)
      })
      .catch( err => console.log(err) )
    }
  }
});
