<template>
<div class="container row justify-content-center">
  <div class="form-group col-12 col-md-6">
    <input type="text" v-model="searchText" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="search by name, description, etc">
  </div>
  <button @click="search" class="btn btn-primary form-control col-6 col-md-2 mt-3">Search</button>
  <repoList v-if="submitted" v-bind:repos="searchResults"/>
</div>
</template>

<script>
import { mapGetters } from 'vuex'
import repoList from './repo-list';

export default {
    components: { repoList },
    computed: {
        ...mapGetters(['searchResults'])
    },
    data() {
        return {
            searchText: '',
            submitted: false
        }
    },
    methods: {
        search() {
            this.$store.commit('resetSearchResults');
            this.submitted = true;
            this.$store.dispatch('searchRepos', this.searchText);
        }
    }
}
</script>

